.header {
  background: url("../img/header-bg.jpg") no-repeat center center / cover;

  &:before{
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(#000, .7);
    position: absolute;
    z-index: 0;
  }
}

.logo {
  &__wrap {
    padding-top: 34px;
    background: url("../img/logo.svg") no-repeat center top / 30px;
    text-align: center;
    z-index: 2;
    position: relative;
  }

  &__text {
    color: $accent;
  }

  &__inscription {

  }
}

.menu {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  height: calc(100vh - 92px - 67px);

  &__list {

  }

  &__item {
    margin: 15px 0;
  }

  &__link {
    color: #919191;
    font-size: 22px;
    display: inline-block;
    transition: .2s ease;
    position: relative;
    
    &:after{
      content: "";
      width: 0;
      height: 1px;
      background: #797979;
      position: absolute;
      left: 0;
      bottom: -5px;
      transition: .2s ease;
    }

    &:hover {
      color: #ffffff;
      transform: scale(1.1);
      
      &:after{
        width: 100%;
        background: #fff;
      }
    }
  }
}


.social-links {
  text-align: center;
  padding: 20px;
  position: relative;
  z-index: 2;
}

.social-link {
  transition: .2s ease;
  margin: 0 8px;
  color: #ffffff;
  display: inline-block;
  font-size: 20px;

  &:hover {
    transform: scale(1.5);
    color: $accent;
  }
}

.social-link i:before {
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-facebook:before {
  content: "\e900";
}

.icon-instagram:before {
  content: "\ea92";
}

.carousel{
  padding: 0;
  @for $i from 0 through 4{
    &__bg-slider#{$i}{
      background: url("../img/slider#{$i}.jpg") no-repeat center center / cover;
    }
  }

  .center .carousel__overlay{
    opacity: .5;
    z-index: 0;
    pointer-events: none;
  }

  .center .content{
    z-index: 2;
  }
  
  .center .carousel__btn{
    background: $accent;
    border-color: $accent;
  }

  &__item{
    position: relative;
    text-align: center;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0 20px;
  }

  &__text{
    max-width: 510px;
  }

  &__content{
    position: relative;
    z-index: 0;
    color: #ffffff;
  }

  &__btn{
    background: none;
    border: 2px solid #fff;
    display: inline-block;
    margin-top: 30px;
    color: #ffffff;
    padding: 8px 12px;
    border-radius: 2px;
    transition: .2s ease;
    
    &:hover{
      transform: scale(1.2);
      box-shadow: 0 0 20px 0 $accent;
    }
  }

  &__overlay{
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(#000, 1);
    position: absolute;
    z-index: 1;
    opacity: .7;
    transition: .2s ease;
  }
}

.owl-dots{
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.owl-dot{
  outline: none !important;
}

.owl-dot.active > span{
  background: $accent;
}

.owl-dot > span{
  background: #8c8c8c;
  width: 33px;
  height: 7px;
  display: block;
  margin: 0 5px;
  transition: .2s ease;
}

.feedback{
  background: url("../img/feedback-bg.jpg") no-repeat center center / cover;
  background-attachment: fixed;
  padding: 60px 20%;
  text-align: center;

  &:before{
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(#000, .5);
    position: absolute;
    z-index: 0;
  }

  &__text{
    font-size: 14px;
    color: #ffffff;
    position: relative;
    z-index: 3;
    padding: 0 20%;
    margin: 20px 0;
  }

  &__form{
    position: relative;
    z-index: 3;
    max-width: 600px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }

  &__input{
    width: 100%;
    margin-bottom: 24px;
    border: 1px solid #fff;
    background: rgba(#fff, .3);
    padding: 18px 12px;
    border-radius: 2px;
    outline: none !important;
    color: #ffffff;
    transition: .2s ease;

    &::placeholder{
      text-transform: uppercase;
      color: #ffffff;
      transition: .2s ease;
    }

    &-name,
    &-tel{
      width: 48%;
    }
    
    &:focus{
      transform: scale(1.1);
      border-color: $accent;
      box-shadow: 0 0 20px 0 #fff;

      &::placeholder{
        font-size: 12px;
      }
    }
    
    &-sbm{
      background: #28292b;
      border-color: #28292b;
      cursor: pointer;

      &:hover{
        border-color: #ffffff;
      }
      
      &:focus{
        transform: none;
        box-shadow: none;
        border-color: #28292b
      }

      &:active{
        box-shadow: inset 0 0 20px 0 #fff;
        border-color: $accent;;
      }
    }
  }

  &__link{
    position: relative;
    font-size: 14px;
    color: #adadad;
    text-align: center;
    z-index: 3;
    transition: .2s ease;

    &:after{
      content: "";
      width: 0;
      height: 1px;
      background: #797979;
      position: absolute;
      left: 0;
      bottom: -3px;
      transition: .2s ease;
    }

    &:hover {
      color: #ffffff;
      transform: scale(1.1);

      &:after{
        width: 100%;
        background: #fff;
      }
    }
  }
}

.map{
  height: 70vh;
  padding: 0;
  
  &:hover:before{
    background: rgba(#000,0);
  }

  &:before{
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(#000, .5);
    position: absolute;
    z-index: 2;
    pointer-events: none;
    transition: .2s ease;
  }
}

.olMapViewport{
  z-index: 1;
}

.footer{
  background: #000;
  padding: 20px 0 0 0;
}