@media screen and (max-width:1199px) {
  .feedback__text{
    padding: 0;
  }

  .feedback{
    padding: 60px 15%;
  }
}

@media screen and (max-width:991px) {
  .feedback {
    padding: 60px 9%;
  }
}

@media screen and (max-width:767px) {
.feedback__form{
  justify-content: center;

}
  .feedback__input-name, .feedback__input-tel {
    width: 100%;
  }
}

@media screen and (max-width:575px) {
  .owl-dot>span {
    width: 28px;
  }
}