body,
html{
    height:100%;
}

body {
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    color: #797979;
    background: #3f3f3f;
    font-weight: 400;
    min-width: 320px;
    overflow-x: hidden;
    line-height: 1.65rem;
}

*{
    box-sizing: border-box;
}

h1,
h2,
h3 {
    margin: 0;
    letter-spacing: 3px;
    color: #fff;
    font-weight: 600;
    z-index: 3;
    position: relative;
}

.first__title {
    font-size: 45px;
}

.second-title {
    font-size: 40px;
    margin-bottom: 54px;
    line-height: 3rem;
}

.third-title {
    font-size: 30px;
    letter-spacing: 2px;
    line-height: 2rem;
}

ul,
li {
    margin: 0;
    padding: 0;
    display: block;
}

p {
    margin: 0;
}

img {
    max-width: 100%;
    height: auto;
    display: block;
}

a {
     text-decoration: none;

    &:hover {
        text-decoration: none;
    }
}

header {
    position: relative;
    height: 100vh;
    padding:10px 10px;
}

section,
footer {
    position: relative;
    padding: 10px 10px;
}