body, html { height: 100%; }

body { font-family: 'Open Sans', sans-serif; font-size: 16px; color: #797979; background: #3f3f3f; font-weight: 400; min-width: 320px; overflow-x: hidden; line-height: 1.65rem; }

* { box-sizing: border-box; }

h1, h2, h3 { margin: 0; letter-spacing: 3px; color: #fff; font-weight: 600; z-index: 3; position: relative; }

.first__title { font-size: 45px; }

.second-title { font-size: 40px; margin-bottom: 54px; line-height: 3rem; }

.third-title { font-size: 30px; letter-spacing: 2px; line-height: 2rem; }

ul, li { margin: 0; padding: 0; display: block; }

p { margin: 0; }

img { max-width: 100%; height: auto; display: block; }

a { text-decoration: none; }

a:hover { text-decoration: none; }

header { position: relative; height: 100vh; padding: 10px 10px; }

section, footer { position: relative; padding: 10px 10px; }

.header { background: url("../img/header-bg.jpg") no-repeat center center/cover; }

.header:before { content: ''; top: 0; left: 0; right: 0; bottom: 0; background: rgba(0, 0, 0, 0.7); position: absolute; z-index: 0; }

.logo__wrap { padding-top: 34px; background: url("../img/logo.svg") no-repeat center top/30px; text-align: center; z-index: 2; position: relative; }

.logo__text { color: #e22838; }

.menu { display: flex; justify-content: center; align-items: center; flex-direction: column; text-align: center; height: calc(100vh - 92px - 67px); }

.menu__item { margin: 15px 0; }

.menu__link { color: #919191; font-size: 22px; display: inline-block; transition: .2s ease; position: relative; }

.menu__link:after { content: ""; width: 0; height: 1px; background: #797979; position: absolute; left: 0; bottom: -5px; transition: .2s ease; }

.menu__link:hover { color: #ffffff; transform: scale(1.1); }

.menu__link:hover:after { width: 100%; background: #fff; }

.social-links { text-align: center; padding: 20px; position: relative; z-index: 2; }

.social-link { transition: .2s ease; margin: 0 8px; color: #ffffff; display: inline-block; font-size: 20px; }

.social-link:hover { transform: scale(1.5); color: #e22838; }

.social-link i:before { font-family: 'icomoon' !important; speak: none; font-style: normal; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon-facebook:before { content: "\e900"; }

.icon-instagram:before { content: "\ea92"; }

.carousel { padding: 0; }

.carousel__bg-slider0 { background: url("../img/slider0.jpg") no-repeat center center/cover; }

.carousel__bg-slider1 { background: url("../img/slider1.jpg") no-repeat center center/cover; }

.carousel__bg-slider2 { background: url("../img/slider2.jpg") no-repeat center center/cover; }

.carousel__bg-slider3 { background: url("../img/slider3.jpg") no-repeat center center/cover; }

.carousel__bg-slider4 { background: url("../img/slider4.jpg") no-repeat center center/cover; }

.carousel .center .carousel__overlay { opacity: .5; z-index: 0; pointer-events: none; }

.carousel .center .content { z-index: 2; }

.carousel .center .carousel__btn { background: #e22838; border-color: #e22838; }

.carousel__item { position: relative; text-align: center; height: 90vh; display: flex; justify-content: center; align-items: center; flex-direction: column; padding: 0 20px; }

.carousel__text { max-width: 510px; }

.carousel__content { position: relative; z-index: 0; color: #ffffff; }

.carousel__btn { background: none; border: 2px solid #fff; display: inline-block; margin-top: 30px; color: #ffffff; padding: 8px 12px; border-radius: 2px; transition: .2s ease; }

.carousel__btn:hover { transform: scale(1.2); box-shadow: 0 0 20px 0 #e22838; }

.carousel__overlay { content: ''; top: 0; left: 0; right: 0; bottom: 0; background: black; position: absolute; z-index: 1; opacity: .7; transition: .2s ease; }

.owl-dots { height: 10vh; display: flex; justify-content: center; align-items: center; flex-wrap: wrap; }

.owl-dot { outline: none !important; }

.owl-dot.active > span { background: #e22838; }

.owl-dot > span { background: #8c8c8c; width: 33px; height: 7px; display: block; margin: 0 5px; transition: .2s ease; }

.feedback { background: url("../img/feedback-bg.jpg") no-repeat center center/cover; background-attachment: fixed; padding: 60px 20%; text-align: center; }

.feedback:before { content: ''; top: 0; left: 0; right: 0; bottom: 0; background: rgba(0, 0, 0, 0.5); position: absolute; z-index: 0; }

.feedback__text { font-size: 14px; color: #ffffff; position: relative; z-index: 3; padding: 0 20%; margin: 20px 0; }

.feedback__form { position: relative; z-index: 3; max-width: 600px; margin: 0 auto; display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap; }

.feedback__input { width: 100%; margin-bottom: 24px; border: 1px solid #fff; background: rgba(255, 255, 255, 0.3); padding: 18px 12px; border-radius: 2px; outline: none !important; color: #ffffff; transition: .2s ease; }

.feedback__input::placeholder { text-transform: uppercase; color: #ffffff; transition: .2s ease; }

.feedback__input-name, .feedback__input-tel { width: 48%; }

.feedback__input:focus { transform: scale(1.1); border-color: #e22838; box-shadow: 0 0 20px 0 #fff; }

.feedback__input:focus::placeholder { font-size: 12px; }

.feedback__input-sbm { background: #28292b; border-color: #28292b; cursor: pointer; }

.feedback__input-sbm:hover { border-color: #ffffff; }

.feedback__input-sbm:focus { transform: none; box-shadow: none; border-color: #28292b; }

.feedback__input-sbm:active { box-shadow: inset 0 0 20px 0 #fff; border-color: #e22838; }

.feedback__link { position: relative; font-size: 14px; color: #adadad; text-align: center; z-index: 3; transition: .2s ease; }

.feedback__link:after { content: ""; width: 0; height: 1px; background: #797979; position: absolute; left: 0; bottom: -3px; transition: .2s ease; }

.feedback__link:hover { color: #ffffff; transform: scale(1.1); }

.feedback__link:hover:after { width: 100%; background: #fff; }

.map { height: 70vh; padding: 0; }

.map:hover:before { background: rgba(0, 0, 0, 0); }

.map:before { content: ''; top: 0; left: 0; right: 0; bottom: 0; background: rgba(0, 0, 0, 0.5); position: absolute; z-index: 2; pointer-events: none; transition: .2s ease; }

.olMapViewport { z-index: 1; }

.footer { background: #000; padding: 20px 0 0 0; }

@media screen and (max-width: 1199px) { .feedback__text { padding: 0; }
  .feedback { padding: 60px 15%; } }

@media screen and (max-width: 991px) { .feedback { padding: 60px 9%; } }

@media screen and (max-width: 767px) { .feedback__form { justify-content: center; }
  .feedback__input-name, .feedback__input-tel { width: 100%; } }

@media screen and (max-width: 575px) { .owl-dot > span { width: 28px; } }
